// in src/App.js
import React from "react";
import { Admin, Resource } from "react-admin";
import { PostList, PostEdit, PostCreate } from "./posts";
import { UserList } from "./users";
import { ChekTransaksi } from "./transactions";
import { ItemList } from "./Items";
import { ScheduleList } from "./schedules";
import { MyAccount } from "./MyAccount";
import { UpdateHarga } from "./UpdateHarga";
import { trashpurchase } from "./TrashPurchase";
import { createMuiTheme } from "@material-ui/core/styles";
import { CommentList } from "./comments.js";
import index from "@material-ui/core/colors/index";
import Dashboard from "./Dashboard";
import authProvider from "./authProvider";
//import dataProvider from './dataProvider';
import jsonServerProvider from "ra-data-json-server";
//import simpleRestProvider from "ra-data-json-server";
import PostIcon from "@material-ui/icons/Book";
import UserIcon from "@material-ui/icons/Group";
import AddShoppingCart from "@material-ui/icons/AddShoppingCart";
import List from "@material-ui/icons/List";
import DateRange from "@material-ui/icons/DateRange";
import AccountBox from "@material-ui/icons/AccountBox";
import PersonAdd from "@material-ui/icons/PersonAdd";
import AddCircleOutline from "@material-ui/icons/AddCircleOutline";
//import { formatMs } from "@material-ui/core/styles/transitions";

const dataProvider = jsonServerProvider(
  // "http://localhost:3000"
  "https://my-json-server.typicode.com/dodinurkasih/jsonserver"
);
const theme = createMuiTheme({
  palette: {
    primary: index,
    secondary: {
      main: "#00e5ff"
    }
  }
});
const App = () => (
  <Admin
    title
    theme={theme}
    dashboard={Dashboard}
    authProvider={authProvider}
    dataProvider={dataProvider}
  >
    <Resource
      name="posts"
      list={PostList}
      edit={PostEdit}
      create={PostCreate}
      icon={PostIcon}
    />
    <Resource name="users" list={UserList} icon={UserIcon} />
    <Resource name="transactions" list={ChekTransaksi} icon={AddShoppingCart} />
    <Resource name="Items" list={ItemList} icon={List} />
    <Resource name="schedules" list={ScheduleList} icon={DateRange} />
    <Resource name="MyAccount" list={MyAccount} icon={AccountBox} />
    <Resource
      name="Add/Update Pricing"
      list={UpdateHarga}
      icon={AddCircleOutline}
    />
    <Resource name="Trash Purchase" list={trashpurchase} icon={AccountBox} />
    <Resource name="comment" list={CommentList} icon={PersonAdd} />
  </Admin>
);

export default App;
